<template>
  <div class="py-spacing-100">
    <div
      class="mb-spacing-100 flex flex-col gap-spacing-200 ipad:flex-row ipad:items-center"
    >
      <h3 class="text-overline/l/relaxed/500 text-content-on-neutral-primary">
        PRICES
      </h3>
      <Search
        placeholder="Search..."
        @search="searchByProductName"
        label="Product"
        search-id="product-name-search"
      />
      <Search
        placeholder="Search..."
        @search="searchByMRName"
        label="Market Research"
        search-id="market-research-name-search"
      />
    </div>
    <ClassicTable
      :headers="headers"
      class="mt-spacing-200"
      :bgClass="'bg-transparent'"
      :horizontal-padding="'px-spacing-0'"
    >
      <template #table-body v-if="filteredPrices?.length > 0">
        <tr
          v-for="(priceRecord, index) in filteredPrices"
          :key="index"
          class="border-b border-stroke-medium text-left text-body/m/relaxed/400 text-content-on-neutral-primary ipad:text-body/l/relaxed/400"
        >
          <td class="py-spacing-100 pr-spacing-100">
            <span
              class="text-body/m/relaxed/400 text-content-on-neutral-secondary"
              >{{ priceRecord?.marketResearchId?.dateStart | toDate }}</span
            >
            <p
              v-if="priceRecord?.marketResearchId?._id"
              class="cursor-pointer hover:text-hover-constant-brand-primary"
              @click.stop="goToMarketResearch(priceRecord)"
              v-html="
                $highlightMatch(
                  priceRecord?.marketResearchId?.name,
                  marketResearchName,
                )
              "
            />
            <p v-else>
              {{ priceRecord?.type }}
            </p>
          </td>
          <td class="py-spacing-100 pr-spacing-100">
            <span
              v-if="priceRecord?.variableId"
              class="text-body/m/relaxed/400 italic text-content-on-neutral-secondary"
              >[{{ priceRecord?.variableId?.name }},
              {{ priceRecord?.variableId?.coefficient }}]</span
            >
            <span
              v-else
              class="text-body/m/relaxed/400 italic text-content-on-neutral-secondary"
              >[default, 1]</span
            >
            <p
              v-html="
                $highlightMatch(priceRecord?.productId?.title, productName)
              "
            ></p>
          </td>
          <td class="whitespace-nowrap py-spacing-100 pr-spacing-100">
            {{ priceRecord?.price | toCurrency }}
          </td>
          <td class="py-spacing-100 pr-spacing-100">
            {{
              (priceRecord?.price * (priceRecord?.variableId?.coefficient || 1))
                | toCurrency
            }}
          </td>
          <td
            class="py-spacing-100 pr-spacing-100 text-content-on-neutral-secondary"
          >
            {{ priceRecord?.productId?.productDetails?.unit || "UNIT" }}
          </td>
          <td
            class="py-spacing-100 pr-spacing-100 text-content-on-neutral-secondary"
          >
            NA
          </td>
          <td
            class="py-spacing-100 pr-spacing-100 text-content-on-neutral-secondary"
          >
            NA
          </td>
          <td class="py-spacing-100 pr-spacing-100">
            {{ priceRecord?.dateOfPriceAdded | toDate }}
          </td>
          <td class="py-spacing-100 pr-spacing-100">
            <DownloadAndView
              v-if="priceRecord?.quotationFile"
              :file="priceRecord?.quotationFile"
            />
            <p v-else class="text-content-on-neutral-tertiary">Not Available</p>
          </td>
        </tr>
      </template>
      <template v-else #table-body>
        <tr>
          <td colspan="9" class="">
            <div
              class="flex flex-col items-center justify-center gap-spacing-200 py-spacing-1000 text-center text-headline/s/relaxed/500 text-content-on-neutral-tertiary"
            >
              <i class="fa fa-exclamation-circle scale-[1.5]"></i>
              <p>No records found</p>
            </div>
          </td>
        </tr>
      </template>
    </ClassicTable>
  </div>
</template>

<script>
export default {
  props: {
    prices: {
      type: Array,
      required: false,
      default: () => [],
    },
    companyId: {
      type: String,
      required: true,
    },
  },
  components: {
    ClassicTable: () => import("@/components/General/Table/ClassicTable.vue"),
    DownloadAndView: () => import("@/components/Quotation/DownloadAndView.vue"),
    Search: () => import("@/components/General/Search.vue"),
  },
  data() {
    return {
      headers: [
        {
          key: "origin",
          value: "Origin",
          sortable: false,
        },
        {
          key: "productName",
          value: "Product",
          sortable: false,
        },
        {
          key: "price",
          value: "Price",
          sortable: false,
        },
        {
          key: "=price",
          value: "=Price",
          sortable: false,
        },
        {
          key: "unit",
          value: "Unit",
          sortable: false,
        },
        {
          key: "score",
          value: "Score",
          sortable: false,
        },
        {
          key: "mr",
          value: "MR",
          sortable: false,
        },
        {
          key: "dateInput",
          value: "Date Input",
          sortable: false,
        },
        {
          key: "quotation",
          value: "Quotation",
          sortable: false,
        },
      ],
      productName: "",
      marketResearchName: "",
    };
  },
  computed: {
    filteredPrices() {
      let output = [...this.prices];
      if (this.productName) {
        output = output?.filter((priceRecord) =>
          priceRecord?.productId?.title
            ?.toLowerCase()
            .includes(this.productName?.trim()?.toLowerCase()),
        );
      }
      if (this.marketResearchName) {
        output = output?.filter((priceRecord) =>
          priceRecord?.marketResearchId?.name
            ?.toLowerCase()
            .includes(this.marketResearchName?.trim()?.toLowerCase()),
        );
      }
      return output;
    },
  },
  methods: {
    goToMarketResearch(record) {
      this.$router.push({
        path: `/dashboard/market-researches/${record?.marketResearchId?._id}`,
        query: {
          companyId: this.companyId,
          productId: record?.variableId?._id
            ? record?.variableId?._id
            : record?.productId?._id,
        },
      });
    },
    searchByProductName(query) {
      this.productName = query;
    },
    searchByMRName(query) {
      this.marketResearchName = query;
    },
  },
};
</script>
